<template>
  <div>
    <!--body-->
    <ul class="friends-main">
      <li v-for="(item, index) in data" class="friends-item" @click.stop="do_item_click(item)">
        <div class="friends-item-header">
          <div class="friends-item-img">
            <img width="50" src="../../assets/icon.jpg"/>
          </div>
          <div class="friends-item-title">
            <span>{{item.title}}</span>
            <span>{{item.time|formatDate}}</span>
          </div>
        </div>
        <div class="friends-item-body">
          <div class="friends-item-body-txt"><span>{{item.content.txt|limit_txt}}</span></div>

          <div class="friends-img">
            <img class="friends-img-item" :width="imgLength(item)" :src="url" v-for="url in item.content.urls"/>
          </div>
          <video v-show="false" src="#"></video>
        </div>
        <div class="friends-item-located"></div>
        <div class="friends-item-bottom">
          <div class="browse">浏览3次</div>
          <ul>
            <li @click.stop="doThumbsUp(item)">
              <img width="35" :src="item.is_thumbs_up?thumbs_up.url_true:thumbs_up.url_false"/>
            </li>
            <li @click.stop="doComment(item,index)">
              <img width="25" src="static/img/comment.png"/>
            </li>
            <li @click.stop="doTransmit(item,index)">
              <img width="25" src="static/img/transmit.png"/>
            </li>
          </ul>
        </div>
          <div v-show="!item.review.hide" class="friends-item-review" @click.stop="hide_wrap(item.review,false)">
            <textarea id="review_id" v-model="item.review.txt" rows="2" class="friends-item-review-text" title="评论"></textarea>
            <button class="review-small-btn" size="small" @click.stop="send_review(item.review)">发送</button>
          </div>
        <div v-show="item.thumbsUp>0" class="comment-thumps-up">
          <img width="35" src="static/img/dz_after.png"/>
          <span>{{item.thumbsUp}}个人觉得很赞</span>
        </div>
        <div v-show="item.comment" class="friends-item-bottom-comment">
          <ul v-for="(val, key) in item.comment">
            <li class="comment-li" >
              <span>{{key}}:</span><span>{{val}}</span>
            </li>
          </ul>
        </div>
      </li>
    </ul>
  </div>

</template>

<script>
    export default {
      data() {
        return {
          thumbs_up: {
            url_false: 'static/img/dz_before.png',
            url_true:'static/img/dz_after.png',
          },
          tab_bar:{
            active:0,
          },
          review: {
            index:0,
            hidden: true,
            txt:''
          },
          data: []
        }
      },
      methods: {
        doBack() {
          console.log('back')
        },
        doAdd() {
          this.$router.push({path:'/publish'});
        },
        doThumbsUp(v) {
          let th= v.is_thumbs_up;
          v.is_thumbs_up=!v.is_thumbs_up;
          v.thumbsUp+=th?-1:1;
        },
        doComment(v, i) {
          v.review.hide=false;
        },
        doTransmit(v, i) {
          console.log(v)
        },
        hide_wrap(v,b) {
          v.hide = b;
        },
        send_review(v){
         v.hide =true;
          let comment= this.data[this.review.index].comment;
          console.log(comment)
          if(comment==undefined){
            this.data[this.review.index].comment={}
          }
          this.data[this.review.index].comment.my=v.txt;
           v.txt='';
        },
        imgLength(v){
          let length=v.content.urls.length;
          if(length==1){
            return '98%';
          }else if(length%2==0){
            return '49%';
          }if(length%2==1){
            return '32%';
          }
        },
        do_item_click(v){
          this.$router.push({path:'/detail',query:{id:v.id}});
        }

      },
      updated(){
        if(!this.review.hidden){
          document.getElementById("review_id").focus();
        }
      },
      filters: {
        formatDate(v) {
          let date = new Date(v);
          let year = date.getFullYear(),
            month = date.getMonth() + 1,//月份是从0开始的
            day = date.getDate(),
            hour = date.getHours(),
            min = date.getMinutes(),
            sec = date.getSeconds();
          return year + '-' +
            month + '-' +
            day + ' ' +
            hour + ':' +
            min + ':' +
            sec;
        },
        limit_txt(v){
          return v.length>=129?v.substring(0,129)+'...':v
        }
      },
      directives: {

      },
      created() {
        this.data=Store.state.friends_data;
        console.log(this.data)
      }
    }
</script>

<style scoped>
  .friends-main {
    position: absolute;
    top: 40px;
    left: 0;
    width: 100%;
    height: 90%;
    box-sizing: border-box;
    overflow-y: auto;
    overflow-x: hidden;
    border: 0px solid black;
  }

  .friends-item {
    width: 98%;
    margin: 10px auto;
    overflow-x: hidden;
    background-color: #fff2eb;
    border-radius: 5px;
    border: 1px solid #cccc;
  }

  .friends-item-header {
    padding: 10px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .friends-item-img {
    width: 44px;
    height: 44px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 3px solid #eee;
    overflow: hidden;
  }

  .friends-item-title {
    padding: 10px;
    border: 0px solid yellow;
    flex-direction: column;
    display: flex;
  }

  .friends-item-title span:nth-child(1) {
    color: rgb(72, 4, 102);
    font-size: 16px;
    width: 50px;
  }

  .friends-item-title span:nth-child(2) {
    box-sizing: border-box;
    color: rgb(0, 0, 0);
    font-size: 10px;
  }

  .friends-item-body {
    width: 100%;
    border: 0px solid red;
  }

  .friends-item-body-txt {
    padding: 0 10px 10px 10px;
    font-size: 16px;
  }
  .friends-item-bottom {
    width: 100%;
    height: 30px;
    display: flex;
    margin-bottom: 5px;
    justify-content: center;
    align-items: center;
    border: 0px solid black;
  }

  .friends-item-bottom ul {
    width: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }

  .friends-item-bottom div {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .friends-item-bottom-comment {
    width: 100%;
    border: 0px solid black;
  }

  .friends-item-bottom-comment ul {
    padding: 0 10px 10px 10px;
  }

  .comment-li {
    box-sizing: border-box;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .comment-li span:nth-child(1) {
    font-size: 16px;
    margin-left: 5px;
    color: blue;
  }
  .comment-li span:nth-child(2) {
    font-size: 14px;
  }

  .friends-item-review-wrap {
    height: 100px;
    width: 100%;
    background-color: black;
    /*background-color: rgba(255, 255, 255, 0);*/
    position: relative;
    top: 0;
    left: 0;

  }

  .friends-item-review {
    box-sizing: border-box;
    height:60px;
    width: 100%;
    padding: 10px;
    display: flex;
    border-radius: 3px;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border: 0px solid black;
  }

  .friends-item-review-text {
    width: 100%;
    height: 50px;
    border: 1px solid rgba(148, 148, 148, 0.52);
    border-radius: 3px;
  }

  .review-small-btn{
    width: 100px;
    height: 50px;
    border-radius: 5px;
    background-color: #18B4ED ;
  }

  .comment-thumps-up {
    box-sizing: border-box;
    margin-left: 10px;
    margin-bottom: 5px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .comment-thumps-up span:nth-child(2) {
    font-size: 16px;
    color: blue;
  }

  .browse {
    font-size: 12px;
  }

  .friends-img {
    display: flex;
    margin: 10px;
    background-color: white;
    justify-content: flex-start;
    flex-wrap: wrap;
    align-items: center;
    border-radius: 10px;
    border: 0px solid black;
  }

  .friends-img-item {
    position: relative;
    margin-left: 9px;
    margin-top: 2px;
    display: flex;
    border-radius: 10px;
    background-color: transparent;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 30px;
    border: 0px solid black;
  }
</style>



